@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  color: #f5f5f5;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.App {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100vh;
  background: rgb(66, 78, 100);
  background: linear-gradient(340deg, #3b4351 0%, #565066 105%);
  padding: 0;
  margin: 0;
  overflow: hidden;
}

.main-view {
  margin-left: 20rem;
  margin-right: 4rem;
  margin-bottom: 4rem;
  width: 100%;

  color: white;

  .grid {
    margin: 20px 0;
    height: 90%;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 10px;
  }
}

.chart-container {
  position: relative;
  height: 100%;
}

.chart-wrapper {
  height: 100%;
  width: 100%;
  position: absolute;
}
