$widget-background-color: #00000015;
$button-accent-color: #8f5ebf;
$button-neutral-color: #00000030;

$text-bright-color: white;
$text-neutral-color: #d5d5d5;

button {
  user-select: none;
}

.widget {
  background: $widget-background-color;
  /* padding: 40px 45px 20px; */
  font-family: "Open Sans", sans-serif;
  color: $text-neutral-color;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  margin-right: 10px;
}

.background-none {
  background: none;
}

.widget__wrapper {
  padding: 0px 45px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80%;
}

.row {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.mini {
  height: 50%;
  margin: 0;
}

.small {
  width: 100%;
}

.wide {
  width: 100%;
  height: 100%;
  padding: 0 40px 50px 40px;
}

.full {
  width: 100%;
  height: 100%;
  justify-content: flex-start;
}

.widget__main-info {
  margin: 40px;
}

.widget__header {
  text-transform: uppercase;
  font-weight: 700;
}

.widget__main-value {
  font-size: 96px;
  font-weight: 300;
  color: $text-bright-color;
}

.widget__text {
  font-weight: 700;
  margin-bottom: 40px;
}

.accent-value.green {
  color: #3fda5e;
}

.accent-value.red {
  color: #ff5151;
}

.widget__additional-info {
  display: flex;
  justify-content: space-between;
  text-align: center;
}

.widget__additional-container {
  border-top: 1px solid rgba(255, 255, 255, 0.283);
  width: 50%;
  height: 100%;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.border-right {
  border-right: 1px solid rgba(255, 255, 255, 0.283);
}

.widget__sub-value {
  font-size: 64px;
  font-weight: 300;
  color: $text-bright-color;
}

// Widget Macros
.nutrients__progress-bar {
  width: 82%;
  height: 12px;
  margin: 15px 0;
  background: #c6c6c6;
  position: absolute;

  border-radius: 15px;
}

.amdr.enter {
  opacity: 0;
  transform: translateX(-40px);
  transition: all 400ms ease;
}
.amdr.enter-done {
  z-index: 1;
  position: relative;
  user-select: none;

  opacity: 1;
  transition: all 400ms ease;
}
.amdr.exit {
  opacity: 0;
  transform: translateX(-40px);
  transition: all 400ms ease;
}

/* .hidden {
    opacity: 0;
    transform: translateX(-40px);
    transition: all 400s ease;
} */

.amdr-borders__borders {
  border-left: 2px solid rgb(255, 255, 255);
  border-right: 2px solid rgb(255, 255, 255);
  height: 30px;
}

.amdr-borders__text {
  display: flex;
  justify-content: space-between;
}

.nutrients__progress {
  position: absolute;
  width: 20%;
  height: 12px;
  margin: 15px 0;
  background: #d81818;

  position: absolute;

  stroke-width: 100px;

  border-radius: 15px;
}

.protein {
  background: #36e8d2;
  width: 23%;
}

.fat {
  background: #f9ed37;
  width: 35%;
}

.carbs {
  background: #ea4289;
  width: 50%;
}

.show-button {
  width: 155px;
  height: 65px;
  margin: 40px;

  color: white;
  font-weight: 700;
  font-size: 18px;
  background: $button-neutral-color;

  border-radius: 45px;
  border: none;
  cursor: pointer;
  outline: none;

  box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
}

.show-button:hover {
  background: #00000040;
}

// ============== Today page widgets =======================

.wrap {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  position: absolute;
}

.wrap--justify {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 60px;

  position: absolute;
}

.new-entry-button {
  width: 115px;
  height: 45px;

  color: $text-bright-color;
  font-weight: 700;
  font-size: 18px;
  background: #8f5ebf;

  border-radius: 45px;
  border: none;
  cursor: pointer;
  outline: none;

  box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.1);
  transition: all 200ms ease;

  position: absolute;
}

.new-entry-button.enter-done {
  opacity: 1;
}

.new-entry-button.enter {
  opacity: 0;
}

.new-entry-button.exit {
  opacity: 0;
}

.new-entry-button.hide {
  display: none;
}

.new-entry-button.green {
  background-color: #5ebf68;
  position: relative;
}

.new-entry-button:hover {
  background-color: #e6e6e6;
  box-shadow: 0px 12px 15px rgba(28, 28, 28, 0.1);
  color: #000;
  transform: translateY(-5px);
}

.note-emoji {
  height: 55px;
  width: 55px;

  user-select: none;
  -webkit-user-drag: none;
  cursor: pointer;

  transition: all 0.3s ease 0s;
}

.not-active {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%) brightness(65%);
  transform: translate3d(0, 0, 0);
}

.note-emoji:hover {
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}
.not-active:hover {
  filter: grayscale(50%) brightness(100%);
}

.weight-wrapper {
  display: flex;
  align-items: center;
}

.weight-button {
  padding: 0;
  margin: 0 30px;
  height: 50px;
  width: 50px;

  background: none;
  border-radius: 50%;
  border: 4px solid #ffffff;

  cursor: pointer;
  outline: none;

  color: white;
  font-size: 32px;
  text-align: center;
  box-shadow: 0px 15px 20px rgba(28, 28, 28, 0.1);
  transform: translate3d(0, 0, 0);
  transition: all 0.3s ease 0s;
}

.weight-button:hover {
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}

// AddNewFood

.new-food-form {
  margin: 40px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  font-weight: 700;
  position: relative;
  transition: all 200ms ease;
}

.new-food-form.enter-done {
  opacity: 1;
}

.new-food-form.enter {
  opacity: 0;
}

.new-food-form.exit {
  opacity: 0;
}

.meal-header {
  display: flex;
  flex-direction: column;
  align-items: center;

  position: relative;
}

.meal-header__item {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 80%;
}

.meal-header__item > label {
  margin: 15px 0 5px 0;
  width: 100%;
  text-align: center;
}

.meal-header__item:first-child > input {
  width: 100%;
}
.meal-header__item:last-child > input {
  width: 35%;
}

.dropdown-datalist {
  position: absolute;
  z-index: 1001;
  max-height: 200px;
  width: 80%;
  background-color: rgba(40, 40, 40, 0.776);
  border-radius: 10px;
  list-style: none;
  top: 70px;
  backdrop-filter: blur(6px);
  box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.365);

  overflow: scroll;

  overflow-x: hidden;
}

.dropdown-datalist.hidden {
  display: none;
}

::-webkit-scrollbar {
  width: 5px;
  background-color: none;
  border-radius: 25px;
}

::-webkit-scrollbar-track {
  border-radius: 25px;
}

::-webkit-scrollbar-thumb {
  border-radius: 25px;
  background: #ffffffb5;
  cursor: pointer;
}

.dropdown-datalist__item {
  margin: 5px 0 5px 15px;
  cursor: pointer;
  transition: all 100ms ease;
}

.dropdown-datalist__item:hover {
  color: #3fda5e;
}

// dropdown arrow
.arrow-button {
  width: 0;
  height: 0;
  cursor: pointer;

  position: absolute;
}

.arrow-button .botton {
  margin-right: 20px;

  border: 10px solid transparent;
  border-right: 10px solid #d5d5d5;
}

.nutrition-facts {
  margin: 20px 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.nutrition-facts__item {
  margin: 10px 0;
  display: flex;
}

.nutrition-facts__item > input {
  width: 25%;
}

.value-label {
  font-weight: 700;
}

.value-label.nutrients {
  width: 85px;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.value-input {
  padding: 3px 6px;

  color: white;
  background-color: #00000020;

  border: 1px solid white;
  border-radius: 15px;
  outline: none;

  font-weight: 700;
  font-size: 16px;
  text-align: center;

  -webkit-appearance: none;
  -moz-appearance: none;

  position: relative;
}

.value-input:focus {
  border: 1px solid #5ebf68;
  // outline: 1px solid #5EBF68;;
}

.grams::after {
  content: "g";
  position: absolute;
  margin-left: 8px;
}

.cancel-button {
  padding: 15px;

  align-self: flex-end;

  cursor: pointer;
  background: none;
  outline: none;
  border: none;

  transition: all 200ms ease;

  position: absolute;
  z-index: 1000;
}

.cancel-button:hover {
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -o-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.15);
}

.cancel-button.hide {
  display: none;
}

.cancel-button.enter-done {
  opacity: 1;
}

.cancel-button.enter {
  opacity: 0;
}

.cancel-button.exit {
  opacity: 0;
}

// Settings page

.settings {
  margin: 40px;
}

.setting {
  margin-bottom: 35px;
  display: flex;
}

.setting .value-label {
  font-size: 24px;
  font-weight: 600;
  width: 250px;
}

.setting .value-input {
  font-size: 24px;
  font-weight: 400;
  width: 150px;
  border-radius: 25px;
}

@media screen and (max-width: 1725px) {
  .dummy-calendar {
    margin-bottom: 20px;
  }

  .widget__main-info {
    margin: 20px;
  }

  .widget__main-value {
    font-size: 36px;
  }

  .widget__sub-value {
    font-size: 24px;
  }

  .widget__additional-container {
    padding: 20px;
  }

  .widget.wide {
    padding: 20px;
  }

  /* Dummy calendar */
  .calendar-grid__day {
    height: 16px;
    width: 16px;
  }

  .calendar__day {
    font-size: 16px;
    height: 16px;
    width: 16px;
  }
}
